@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;900&family=Tinos:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}.App{background:#312929}.Navigation{padding:2% 0}.Navigation ul{display:flex;list-style-type:none;justify-content:space-around}.Navigation ul li{color:white;font-size:2.2rem}.Navigation ul li:nth-of-type(1){color:#bb7043}.Hero{text-align:center;font-family:Tinos;color:white;padding:5% 0;line-height:60px;background:url(/reactjs-ginjoint/static/media/Header.47878f24.png) center/cover no-repeat}.Hero p{font-size:3.5rem;font-weight:bold}.Hero p:nth-of-type(1){padding:12.1% 0 0 0}.Hero p:nth-of-type(2){padding:0 0 12.1% 0}.Hero h1{font-size:6rem;font-weight:bold}.SectionRepeat{background:#211e1f;color:white;display:flex;padding:11% 0}.SectionRepeat article:nth-of-type(1){padding:5% 5% 0 8%;width:60%}.SectionRepeat article:nth-of-type(2){width:50%}.SectionRepeat h2{font-size:3.5rem;line-height:50px;padding:5% 0}.SectionRepeat p{font-size:1.5rem;padding:0 23% 5% 0}.SectionRepeat img{width:100%}.SectionRepeat button{padding:2% 5%;background:#bb7043;border:none;color:#211e1f}.Gallery{background:#bb7043}.Gallery h2{font-size:1.2rem;padding:10% 12% 10% 8%;font-weight:400;text-transform:uppercase}.Gallery article{display:flex}.Gallery article section{margin:0 2%}.Gallery article section span{color:white;display:block;text-align:center;padding:27% 0;font-size:40px;font-weight:500;text-shadow:0 3px 5px black;width:55%;margin:0 auto}.Gallery article section p{color:white;line-height:25px}.Gallery article section p:nth-of-type(1){padding:8% 0 0 0}.Gallery article section p:nth-of-type(2){padding:0 0 8% 0}.Gallery article section h3{padding:0 0 30% 0;font-weight:300}.galley1{background:url(/reactjs-ginjoint/static/media/Rectangle.0c3804d2.png) center/cover no-repeat;height:300px}.galley2{background:url(/reactjs-ginjoint/static/media/Rectangle2.cc232df1.png) center/cover no-repeat;height:300px}.galley3{background:url(/reactjs-ginjoint/static/media/Rectangle3.8087e487.png) center/cover no-repeat;height:300px}.DoubleSection{display:flex;text-align:center;color:white;margin:0% 0 0 0}.DoubleSection div{border:1px solid grey}.DoubleSection div img{height:600px;width:400px;margin:10% 0 0 0}.DoubleSection div:nth-of-type(1){padding:0 12% 0 0}.DoubleSection div:nth-of-type(1) h2{width:70%;margin:0 auto;padding:5% 0;font-size:2.5rem;font-weight:400}.DoubleSection div:nth-of-type(1) p{padding:25.5% 0 0 0}.DoubleSection div:nth-of-type(2) img{height:600px;width:400px;margin:10% 0 0 0}.DoubleSection div:nth-of-type(2) h2{width:62%;margin:0 auto;padding:5% 0;font-size:2.5rem;font-weight:400}.DoubleSection div:nth-of-type(2) p{padding:5% 0}.FooterBar{display:flex;justify-content:space-around;padding:3% 0;color:white}.FooterBar>div img{margin:0 0 0 90%}.FooterBar ul{list-style-type:none;line-height:19px}.FooterBar ul p{color:orange;padding:0 0 12% 0}.FooterBar ul:nth-of-type(1){margin:0 0 0 30%}.FooterBar ul:nth-of-type(3){margin:1.5% 0 0% 0}.Bathtub{display:flex;justify-content:center;align-items:center}.Bathtub div:nth-of-type(1){margin:0 auto;padding:0 0 0 24%}.Bathtub div:nth-of-type(2){margin-left:auto;padding:0 10% 0 0}

